body {
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
}

#app {
  width: 100%;
  margin: 0;
  padding: 0;
}

#htmx-result {
  padding: 2rem 0;
}

.logo {
  will-change: filter;
  filter: drop-shadow(0 0 2em #f3c077aa);
  height: 12em;
  padding: 2em;
  transition: filter .3s;
  display: inline;
}

.content {
  padding: 2em;
}

.read-the-docs {
  color: #888;
}

button {
  color: #fefef5;
  cursor: pointer;
  background-color: #1a1a1a;
  border: 1px solid #0000;
  border-radius: 8px;
  padding: .6em 1.2em;
  font-family: inherit;
  font-size: 1em;
  font-weight: 500;
  transition: border-color .25s;
}

button:hover {
  border-color: #646cff;
}

button:focus, button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

.button-link {
  color: #fefef5;
  cursor: pointer;
  background-color: #1a1a1a;
  border: 1px solid #0000;
  border-radius: 8px;
  padding: .6em 1.2em;
  font-family: inherit;
  font-size: 1em;
  font-weight: 500;
  text-decoration: none;
  transition: border-color .25s;
  display: inline-block;
}

.button-link:hover {
  color: #fefef5;
  border-color: #646cff;
}

.btn-primary, .btn-secondary, .btn-accent, .btn-outline:hover {
  font-weight: 600;
  color: #0f172a !important;
}

.badge-primary, .badge-secondary, .badge-accent {
  font-weight: 500;
  color: #0f172a !important;
}

.hero-gradient {
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 50%, #334155 100%);
}

.glass-card {
  backdrop-filter: blur(10px);
  background: #1e293b80;
  border: 1px solid #94a3b81a;
}

.glow-primary {
  box-shadow: 0 0 20px #6366f14d;
}

.skill-icon {
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
}

.skill-icon:hover {
  filter: drop-shadow(0 10px 20px #6366f14d);
  transform: translateY(-4px)scale(1.05);
}

.project-card {
  border: 1px solid #94a3b81a;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
}

.project-card:hover {
  border-color: #6366f14d;
  transform: translateY(-8px);
  box-shadow: 0 20px 40px #0000004d, 0 0 20px #6366f133;
}

.stat-enhanced {
  backdrop-filter: blur(10px);
  background: linear-gradient(135deg, #1e293bcc 0%, #33415599 100%);
  border: 1px solid #94a3b81a;
}

.btn-glow {
  transition: all .3s;
  position: relative;
  overflow: hidden;
}

.btn-glow:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff3, #0000);
  width: 100%;
  height: 100%;
  transition: left .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.btn-glow:hover:before {
  left: 100%;
}

.navbar-modern {
  backdrop-filter: blur(20px);
  background: #0f172acc;
  border-bottom: 1px solid #94a3b81a;
}

.text-gradient {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(135deg, #6366f1, #06b6d4, #f59e0b) 0 0 / 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  animation: 3s infinite gradient-shift;
}

@keyframes gradient-shift {
  0%, to {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }
}

.floating {
  animation: 3s ease-in-out infinite floating;
}

@keyframes floating {
  0%, to {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

[x-cloak] {
  display: none !important;
}

@keyframes progress {
  50% {
    background-position-x: -115%;
  }
}

@keyframes radio {
  0% {
    padding: 5px;
  }

  50% {
    padding: 3px;
  }
}

@keyframes toast {
  0% {
    opacity: 0;
    scale: .9;
  }

  to {
    opacity: 1;
    scale: 1;
  }
}

@keyframes dropdown {
  0% {
    opacity: 0;
  }
}

@keyframes rating {
  0%, 40% {
    filter: brightness(1.05) contrast(1.05);
    scale: 1.1;
  }
}

@keyframes skeleton {
  0% {
    background-position: 150%;
  }

  to {
    background-position: -50%;
  }
}

